export default {
	data() {
		return {
			aboutTitle:"",
			aboutVersions:"",
			aboutImg:""
		}
	},
	onLoad() {
		this.aboutIndex()
	},
	methods: {
		async aboutIndex(){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/about"
			})
			that.aboutTitle = res.data.data.title
			that.aboutVersions = res.data.data.shorttitle
			that.aboutImg = res.data.data.thumb
		}
	}
}
